.login-form {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* UI Properties */
    background: transparent linear-gradient(180deg, #000000 0%, #000E09 100%) 0% 0% no-repeat padding-box;
    mix-blend-mode: normal;
    border-radius: 0; 
    opacity: 1;

}
.logo {
    margin-bottom: 20px;
    width: 238px;
    height: 90px;
}
.text ,
.forgot-password {
top: 267px;
left: 108px;
width: 238px;
height: 20px;
/* UI Properties */
text-align: center;
font: normal normal normal 16px/21px Mulish;
letter-spacing: 0.02px;
color: #36A546CC;
opacity: 1;
}

.form-box {
    position: relative;
    width: 400px;
    height: 450px;
    background: transparent;
    border: none;
    border-radius: 20px;
    backdrop-filter: blur(15px) brightness(80%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.login-form label {
    display: block;
    margin-bottom: 10px;
  }

.login-form input[type="text"],
.login-form input[type="password"],
.login-form button {
left: 61px;
width: 238px;
height: 42px;
/* UI Properties */
background: #1F191966 0% 0% no-repeat padding-box;
box-shadow: inset 0px 3px 3px #FFFDFD40, 3px 3px 3px #4A494947;
mix-blend-mode: normal;
border-radius: 28px;
margin-bottom: 10px;
opacity: 1;
  }

  input[type="text"]::placeholder,
  input[type="password"]::placeholder {
    text-align: left;
    padding-left: 10px; 
    
}
  
  .login-form button {
    background: transparent linear-gradient(180deg, #00FF2580 0%, #000000 100%, #36A54680 100%) 0% 0% no-repeat padding-box;
  }
  
  .login-form button:hover {
    background-color: #0056b3;
  }
